body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
  position: relative;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch-hands {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.stopwatch-hands-minutes {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  top: 20vmin;
}

.stopwatch-hands-seconds {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear 1s infinite rotate;
  position: absolute;
  top: 2vmin;
}

.stopwatch-center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch-hands-seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch-hands-minutes {
  animation-duration: 600s;
}
